export default (_xyz) => {
  if (!_xyz.hooks)
    return null;
  const current = {
    layers: [],
    locations: []
  };
  window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, (match, key, value) => {
    if (current[key]) {
      current[key] = decodeURI(value).split(",");
    } else {
      current[key] = decodeURI(value);
    }
  });
  if (current.token) {
    window.history.replaceState({}, document.title, window.location.href.replace(new RegExp(`token=${current.token}`), ""));
    delete current.token;
  }
  return {
    current,
    set,
    remove,
    removeAll,
    push,
    filter
  };
  function set(hooks) {
    Object.assign(_xyz.hooks.current, hooks);
    pushState();
  }
  function remove(key) {
    if (Array.isArray(_xyz.hooks.current[key])) {
      _xyz.hooks.current[key].length = 0;
    } else {
      delete _xyz.hooks.current[key];
    }
    pushState();
  }
  function removeAll() {
    Object.keys(_xyz.hooks.current).forEach((key) => remove(key));
    pushState();
  }
  function push(key, val) {
    if (_xyz.hooks.current[key]) {
      if (_xyz.hooks.current[key].indexOf(val) < 0)
        _xyz.hooks.current[key].push(val);
    } else {
      _xyz.hooks.current[key] = [val];
    }
    pushState();
  }
  function filter(key, val) {
    _xyz.hooks.current[key] = _xyz.hooks.current[key].filter((el) => el !== val);
    pushState();
  }
  function pushState() {
    try {
      window.history.pushState({}, document.title, `?${_xyz.utils.paramString(_xyz.hooks.current)}`);
    } catch (me) {
      console.log(me);
    }
  }
};
