export default (_xyz) => {
  return {
    init
  };
  function init(params) {
    _xyz.layers.listview.node = params.target;
    _xyz.layers.listview.groups = {};
    Object.values(_xyz.layers.list).forEach((layer) => {
      if (layer.hidden)
        return;
      _xyz.layers.view.create(layer);
      if (!layer.group) {
        _xyz.layers.listview.node.appendChild(layer.view);
        return;
      }
      if (!_xyz.layers.listview.groups[layer.group])
        createGroup(layer);
      _xyz.layers.listview.groups[layer.group].addLayer(layer);
    });
  }
  function createGroup(layer) {
    const group = {
      list: []
    };
    _xyz.layers.listview.groups[layer.group] = group;
    const drawer = _xyz.layers.listview.node.appendChild(_xyz.utils.html.node`<div class="drawer layer-group expandable">`);
    const header = drawer.appendChild(_xyz.utils.html.node`
      <div
        class="header enabled"
        onclick=${(e) => {
      e.stopPropagation();
      _xyz.utils.toggleExpanderParent(e.target, true);
    }}>
        <span>${layer.group}`);
    const meta = drawer.appendChild(_xyz.utils.html.node`<div class="meta">`);
    group.chkVisibleLayer = () => {
      group.list.some((layer2) => layer2.display) ? hideLayers.classList.add("on") : hideLayers.classList.remove("on");
    };
    group.addLayer = (layer2) => {
      if (layer2.groupmeta) {
        const metaContent = meta.appendChild(_xyz.utils.html.node`<div>`);
        metaContent.innerHTML = layer2.groupmeta;
      }
      group.list.push(layer2);
      drawer.appendChild(layer2.view);
      group.chkVisibleLayer();
    };
    const hideLayers = header.appendChild(_xyz.utils.html.node`
      <button
        class="btn-header xyz-icon icon-toggle"
        title=${_xyz.language.layer_group_hide_layers}
        onclick=${(e) => {
      e.stopPropagation();
      e.target.classList.toggle("on");
      if (e.target.classList.contains("on")) {
        group.list.filter((layer2) => !layer2.display).forEach((layer2) => layer2.show());
        return;
      }
      group.list.filter((layer2) => layer2.display).forEach((layer2) => layer2.remove());
    }}>`);
    header.appendChild(_xyz.utils.html.node`
      <button 
        class="xyz-icon btn-header icon-expander"
        title=${_xyz.language.layer_group_toggle}
        onclick=${(e) => {
      e.stopPropagation();
      _xyz.utils.toggleExpanderParent(e.target);
    }}>`);
  }
};
