import create from "./create.js";
import attribution from "./attribution.js";
import locate from "./locate.js";
import geoJSON from "./geoJSON.js";
import popup from "./popup.js";
import interaction from "./interaction/_interaction.js";
import getBounds from "./getBounds.js";
import icon from "./icon.js";
import infotip from "./infotip.js";
import layer from "./layer/_layer.js";
export default (_xyz) => ({
  create: create(_xyz),
  changeEndEvent: new CustomEvent("changeEnd"),
  attribution: attribution(_xyz),
  locate: locate(_xyz),
  interaction: interaction(_xyz),
  popup: popup(_xyz),
  infotip: infotip(_xyz),
  geoJSON: geoJSON(_xyz),
  icon: icon(_xyz),
  layer: layer(_xyz),
  getBounds: getBounds(_xyz),
  flyToBounds: (extent, params = {}) => {
    _xyz.map.getView().fit(extent, Object.assign({
      padding: [50, 50, 50, 50],
      duration: 1e3
    }, params));
  },
  getZoom: () => {
    return _xyz.map.getView().getZoom();
  }
});
